/* font-family: 'Rubik Iso', cursive; */
@import url("https://fonts.googleapis.com/css2?family=Rubik+Iso&display=swap");

/* font-family: 'Sono', sans-serif; */
@import url("https://fonts.googleapis.com/css2?family=Sono:wght@700&display=swap");

/* zero out all default margins */
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

/* css variables so its easier 
to customize and so I don't repeat myself */
:root {
  --color-bg: #fff;
  --color-bg-variant: #dedede;
  --color-primary: #434343;
  --color-secondary: #00d8b6;
  --color-white: #d9fffc;
  --color-secondary-alt: #00ffed;

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%; /* For medium devices */
  --container-width-sm: 90%; /* For small devices */
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Sono", sans-serif;
  font-weight: 700;
  background: var(--color-bg);
  color: var(--color-primary);
  line-height: 1.7;
  background-image: url("../src/assets/memphis-mini.webp");
  font-size: calc(0.4vw + 12px);
}

/* ======= GENERAL STYLES ========== */
.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 700;
  text-shadow: 1px 1px 2px var(--color-secondary);
}

h1 {
  font-size: 8vw;
  text-shadow: 2px 2px 4px var(--color-secondary);
}

h2 {
  padding-top: 1rem;
}

section {
  padding-top: 12rem;
  min-height: 100vh;
}

section > h2,
section > h5 {
  text-align: center;
  color: var(--color-light);
}

section > h2 {
  margin-bottom: 3rem;
}

.secondary-text {
  text-shadow: 1px 1px 2px var(--color-secondary);
}

a {
  color: var(--color-secondary);
  text-shadow: 1px 1px 2px var(--color-secondary-alt);
  transition: var(--transition);
}

a:hover {
  color: var(--color-secondary-alt);
  text-shadow: 1px 1px 4px var(--color-primary);
}

.btn {
  font-size: 4vw;
  width: max-content;
  display: inline-block;
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-secondary);
  transition: var(--transition);
  box-shadow: 2px 2px 2px var(--color-secondary);
}

.btn:hover {
  padding: 0.751rem 1.21rem;
  background: var(--color-secondary-alt);
  color: var(--color-primary);
  box-shadow: 3px 3px 6px var(--color-primary);
}

.btn-primary {
  border: 1px solid var(--color-primary);
  box-shadow: 2px 2px 2px var(--color-primary);
  background: var(--color-secondary);
  color: var(--color-primary);
  text-shadow: 1px 1px 2px var(--color-primary);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

